.project_card {
  padding: 1em;
  border: 1px solid #7a7a7a;
  border-radius: 5px;
  width: 24%;
  margin: 0.5%;
}
.project_card h4 {
  display: inline-block;
  background-color: #222;
  color: #ffbb33;
  border: 1px solid #7a7a7a;
  padding: 0.4em;
  margin-bottom: 1.8em;
  font-size: 1.3em;
  border-radius: 3px;
  max-width: 100%;
  box-sizing: border-box;
}
.project_card p {
  color: #f6f6f6;
  margin-bottom: 1em;
}
.project_card p span {
  font-weight: bold;
}
.category_text {
  display: flex;
  align-items: center;
}
.category_text span {
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #ccc;
  margin-right: 5px;
}
.category_text .infra {
  background-color: #ffaebc;
}

.category_text .desenvolvimento {
  background-color: #a0e7e5;
}

.category_text .design {
  background-color: #b4f8c8;
}

.category_text .planejamento {
  background-color: #fbe7c6;
}
.project_card_actions {
  margin-top: 1.2em;
  display: flex;
  align-items: center;
}

.project_card_actions a,
.project_card_actions button {
  text-decoration: none;
  background-color: #222;
  color: #ffbb33;
  border: 1px solid #7a7a7a;
  font-size: 0.9em;
  padding: 0.6em 1em;
  margin-right: 1em;
  cursor: pointer;
  transition: 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.project_card_actions svg {
  margin-right: 0.5em;
}

.project_card_actions a:hover,
.project_card_actions button:hover {
  color: #141414;
  background-color: #fff;
}
